import React from 'react';
import Masonry from 'react-masonry-component';

//import logo from './logo.png'


class InfinateScrollContainer extends React.Component {
  componentDidMount() {
    window.addEventListener('scroll', this.onScroll.bind(this), false);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll.bind(this), false);
  }

  onScroll() {
    if (!this.props.isLoading) {
      if ((window.innerHeight + window.scrollY) >= (document.body.offsetHeight - 250)) {
        this.props.maxScrollEvent();
      }
    }
  }

  render() {
    return this.props.children;
  }
};


const RecordsContainer = ({ records, isLoading, requestMoreRecords }) => (
  <div className="wrapper">
    <div className="masonry">

      <InfinateScrollContainer
        isLoading={isLoading}
        maxScrollEvent={() => requestMoreRecords()}
      >
        {records.map(record =>
          <div className="item" key={record.id}>
            <img src={record.thumbUrl} />
            <div>{record.title}</div>
          </div>
        )}
      </InfinateScrollContainer>

    </div>
  </div>
);

export default RecordsContainer;



